import defineReactiver from './index'
/***
 * Observer ‰ºöË¢´ÈôÑÂä†Âà∞ÊØè‰∏Ä‰∏™Ë¢´‰æ¶ÊµãÁöÑObject ‰∏ä„ÄÇ
 * ‰∏ÄÊó¶Ë¢´ÈôÑÂä†‰∏äÔºåObserver‰ºöÂ∞ÜObjectÁöÑÊâÄÊúâÂ±ûÊÄßËΩ¨Êç¢‰∏∫getter/setterÁöÑÂΩ¢Âºè
 * Êù•Êî∂ÈõÜÂ±ûÊÄßÁöÑ‰æùËµñÔºåÂπ∂‰∏îÂΩìÂ±ûÊÄßÂèëÁîüÂèòÂåñÊó∂‰ºöÈÄöÁü•Ëøô‰∫õ‰æùËµñ
*/
export class Observer{
    constructor(value) {
        this.value = value

        if (!Array.isArray(value)) {
            this.walk(value)
        }
    }
    /***
     * walk‰ºöÂ∞ÜÊØè‰∏Ä‰∏™Â±ûÊÄßÈÉΩËΩ¨Êç¢Êàêgetter/setterÁöÑÂΩ¢ÂºèÊù•‰æ¶ÊµãÂèòÂåñ
     * Ëøô‰∏™ÊñπÊ≥ïÂè™ÊúâÂú®Êï∞ÊçÆÁ±ªÂûã‰∏∫ObjectÊó∂Ë¢´Ë∞ÉÁî®
    */
    walk(obj) {
        const keys = Object.keys(obj)
        for(let i = 0; i < keys.length; i++) {
            defineReactiver(obj, keys[i], obj[keys[i]])
        }
    }
}